.hero-banner {
    @media (max-width: 991.9999px) {
        padding: 0;
    }
}

.home-banner {
    background-color: #fff;
    padding: 20px 0;

    & img {
        max-width: 300px;
        border: 1px solid #eee;
        border-radius: 4px;
    }

    

    @media (max-width: 700px) {
        & img {
            max-width: 100% !important;
        }
        & .c1 {
            flex-basis: 100%;
        }
        & .c2 {
            flex-basis: 100%;
        }
    }
}

.services {
    background-color: rgb(248,249,250);
    padding: 30px 20px 15px;
    & h6 {
        color: #000;
        margin-bottom: 8px;
    }
}

.categories {
    border-radius: 12px;
    & img {
        &:hover {
            opacity: 0.8;
        }
    }
}